import React from 'react';

const imgStyle = {
  width: '100%',
};

export default function Report2016() {
  return (
    <img
     style={imgStyle}
     src="../static/img/2016-EOY-report-highres.jpg"
     alt="2016 End of Year Report"
    />
  );
}



