	module.exports = "<h1 id=want-to-help->Want to Help?</h1><h2 id=table-of-contents>Table of Contents</h2><ul><li><a href=#background-information>Background Information</a></li><li><a href=#current-projects>Current Projects</a></li><li><a href=#contributing>Contributing</a></li><li><a href=#tools>Tools</a></li><li><a href=#additional-questions>Additional Questions</a></li></ul><h2 id=background-information>Background Information</h2><p><strong>WE ARE SO EXCITED YOU ARE HERE!</strong></p><p>You are on your way to some serious civic hacking.</p><h4 id=so-how-do-i-get-involved->So how do I get involved?</h4><p>Start by joining our Meetup.com group at <a href=http://www.meetup.com/CodeForDenver>www.meetup.com/CodeForDenver</a>. This is where we list all of our upcoming meetings and events. We meet weekly to work on projects. RSVP or just drop by and say hello.</p><h4 id=how-many-people-are-working-on-a-project-at-any-given-time->How many people are working on a project at any given time?</h4><p>This varies according to the circumstances and there are no hard and fast rules that set a minimum or maximum number of volunteers per project. For the sake of efficiency, we aim to focus our efforts by limiting the number of projects in active development at any given time.</p><h4 id=is-there-any-one-person-who-is-typically-in-charge-of-projects->Is there any one person who is typically in charge of projects?</h4><p>Each project has a scrum master, product owner, and tech lead. Organizers listed at our <a href=\"http://www.meetup.com/CodeForDenver/\">Meetup site</a> work to make sure that the roles are filled.</p><h4 id=i-m-not-a-programmer-or-i-m-not-interested-in-programming-can-i-still-get-involved->I&#39;m not a programmer or I&#39;m not interested in programming. Can I still get involved?</h4><p>Yes! As long as you are willing to collaborate and engage there are numerous ways in which to be involved. As you can tell from our name, coding is a big part of what we do, but helping solve challenges in the community takes more than software expertise. Our brigade volunteers also have backgrounds in marketing, design, sales, facilitation, non-profits, project management, and more. Additional perspectives and experiences are always valued and welcome, so come on down!</p><h4 id=i-m-a-beginner-programmer-can-i-still-get-involved->I&#39;m a beginner programmer. Can I still get involved?</h4><p>Yes! Many of our members have been beginning programmers who have found the environment helpful to improve their abilities while providing something of value.</p><h4 id=what-languages-frameworks-do-you-typically-use->What languages / frameworks do you typically use?</h4><p>We don&#39;t use one particular language or framework all of the time. While we have strong backgrounds in open source, what we use depends on the nature of the project and the groups that we serve.</p><h2 id=current-projects>Current Projects</h2><p>Check out our <a href=https://waffle.io/codefordenver/projects>Waffle Board</a> for a current list of projects. Mmmm, waffles.</p><h2 id=contributing>Contributing</h2><p>A good place to start is by reading our <a href=https://github.com/codefordenver/codeofconduct>Code of Conduct</a>.</p><p>Once you&#39;re done with that, see below for the tools we use to communicate, collaborate, and organize.</p><h2 id=tools>Tools</h2><ul><li><h3 id=meetup-com>Meetup.com</h3><p>Our event organizing tool.</p><p><strong><a href=\"http://www.meetup.com/codefordenver/\">You can sign up for upcoming civic-hack nights and other important events</a>.</strong></p></li><li><h3 id=flowdock-com>Flowdock.com</h3><p>Our <a href=https://www.flowdock.com/app/cfa-brigades/code-for-denver>chat room</a>. We use it to engage with other members, share progress, ask questions, and discuss technology. It&#39;s cross platform so you can use it anywhere.</p><p><strong>Get your account by emailing codefordenver@gmail.com or by attending one of our meetups.</strong></p></li><li><h3 id=google-drive>Google Drive</h3><p>Google drive hosts our group&#39;s <a href=\"https://drive.google.com/folderview?id=0B15HLk4_JV3nWjkyOGtFUmhKZDQ&amp;usp=sharing_eid\">shared documents</a>. You will need to request access to the folder.</p></li><li><h3 id=github-com>Github.com</h3><p>Our source control solution that hosts our <a href=https://github.com/codefordenver>code repositories</a>. This is how we share code between members and other brigades around the world.</p><p><strong><a href=\"https://github.com/\">Get an account here</a>.</strong></p></li><li><h3 id=waffle-io>Waffle.io</h3><p>Our light-weight project management solution. A simple Kanban not unlike Trello with very nice github integration. Once you have a github account, you should have access to the waffle boards over at <a href=\"https://waffle.io/\">waffle.io</a>. Do let us know if you run into any trouble!</p></li></ul><h2 id=additional-questions>Additional Questions</h2><p>You can get in touch with our organizers via our <a href=\"http://www.meetup.com/CodeForDenver/\">Meetup site</a> or drop us a line at codefordenver[at]gmail.com.</p>";

